import { pointsWithinPolygon, bboxClip, nearestPointOnLine, buffer } from '@turf/turf';
self.onmessage = e => {
    const [id, method, params] = e.data;
    const evaluate = (f) => {
        try {
            const result = f();
            self.postMessage([id, result, null], null);
        }
        catch (err) {
            self.postMessage([id, null, err], null);
        }
    };
    switch (method) {
        case 'pointsWithinPolygon': {
            evaluate(() => pointsWithinPolygon(params[0], params[1]));
            break;
        }
        case 'bboxClip': {
            evaluate(() => bboxClip(params[0], params[1]));
            break;
        }
        case 'nearestPointOnLine': {
            evaluate(() => nearestPointOnLine(params[0], params[1]));
            break;
        }
        case 'buffer': {
            evaluate(() => buffer(params[0], params[1]));
            break;
        }
    }
};
