function link(array) {
    if (!(n = array.length))
        return;
    var n, i = 0, a = array[0], b;
    while (++i < n) {
        a.n = b = array[i];
        b.p = a;
        a = b;
    }
    a.n = b = array[0];
    b.p = a;
}
